// purchase-form.tsx
'use client';

import type { EventData, PurchaseFormValues } from '@/lib/types';
import { useForm, Controller } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Textarea } from '@/components/ui/textarea';
import { useToast } from '@/hooks/use-toast';
import { useState, useEffect, useMemo } from 'react';
import { Loader2, ShoppingCart, ClipboardCopy, CreditCard, AlertTriangle, Info, CheckCircle, RefreshCw } from 'lucide-react';
import Image from 'next/image';
import { initiatePurchaseAction, checkPaymentStatusAction } from '@/app/actions/purchase-actions';
import { SERVICE_FEE_PER_TICKET } from '@/lib/constants';

interface PurchaseFormProps {
  event: EventData;
}

const purchaseSchema = z.object({
  nome_comprador: z.string().min(3, { message: 'Nome completo é obrigatório e deve ter pelo menos 3 caracteres.' }),
  email_comprador: z.string().email({ message: 'E-mail inválido.' }),
  whatsapp: z.string().regex(/^\d{10,11}$/, { message: 'WhatsApp inválido. Use apenas números (Ex: 11988887777).' }),
  quantidade: z.number().min(1, { message: 'Selecione pelo menos 1 ingresso.' }).max(5, {message: 'Máximo de 5 ingressos por compra.'}),
});

interface StoredPurchaseDetails {
  eventId: string;
  pixCopyPaste: string;
  qrCodeBase64: string | null;
  saleId: string;
  totalAmount: number;
  mpPaymentId: string;
  mpExternalReference: string;
  formValues?: PurchaseFormValues; // Store form values too
}


export default function PurchaseForm({ event }: PurchaseFormProps) {
  const { toast } = useToast();
  const [isProcessing, setIsProcessing] = useState(false);
  const [isCheckingStatus, setIsCheckingStatus] = useState(false);
  const [showPaymentDetails, setShowPaymentDetails] = useState(false);
  const [currentPixCode, setCurrentPixCode] = useState<string | null>(null);
  const [currentQrCodeBase64, setCurrentQrCodeBase64] = useState<string | null>(null);
  const [currentSaleId, setCurrentSaleId] = useState<string | null>(null); // TrinkaPass internal Sale ID
  const [currentTotalAmount, setCurrentTotalAmount] = useState<number>(0);
  const [currentMpPaymentId, setCurrentMpPaymentId] = useState<string | null>(null);
  const [currentMpExternalReference, setCurrentMpExternalReference] = useState<string | null>(null); // The TRK-... reference
  const [paymentConfirmedByClientCheck, setPaymentConfirmedByClientCheck] = useState(false);
  
  const [maxTickets, setMaxTickets] = useState(5); 

  const storageKey = useMemo(() => `trinkapass_pending_purchase_${event?.id}`, [event?.id]);

  const form = useForm<PurchaseFormValues>({
    resolver: zodResolver(purchaseSchema),
    defaultValues: {
      nome_comprador: '',
      email_comprador: '',
      whatsapp: '',
      quantidade: 1,
    },
  });

  const { register, handleSubmit, control, watch, formState: { errors }, reset, getValues } = form;
  const selectedQuantity = watch('quantidade') || 0;

  const totalEventPrice = useMemo(() => event.preco_ingresso * selectedQuantity, [event.preco_ingresso, selectedQuantity]);
  const totalServiceFee = useMemo(() => SERVICE_FEE_PER_TICKET * selectedQuantity, [selectedQuantity]);
  const finalPurchaseAmount = useMemo(() => totalEventPrice + totalServiceFee, [totalEventPrice, totalServiceFee]);

  useEffect(() => {
    setMaxTickets(Math.min(5, event.quantidade_disponivel));
  }, [event.quantidade_disponivel]);

  // Load pending purchase from localStorage on mount
  useEffect(() => {
    if (!event?.id) return;

    const storedPurchaseDataString = localStorage.getItem(storageKey);
    if (storedPurchaseDataString) {
      try {
        const parsedData: StoredPurchaseDetails = JSON.parse(storedPurchaseDataString);
        if (parsedData.eventId === event.id) {
          setCurrentPixCode(parsedData.pixCopyPaste);
          setCurrentQrCodeBase64(parsedData.qrCodeBase64 || null);
          setCurrentSaleId(parsedData.saleId);
          setCurrentTotalAmount(parsedData.totalAmount);
          setCurrentMpPaymentId(parsedData.mpPaymentId);
          setCurrentMpExternalReference(parsedData.mpExternalReference);
          if (parsedData.formValues) {
            reset(parsedData.formValues); // Restore form values
          }
          setShowPaymentDetails(true);
        } else {
          localStorage.removeItem(storageKey); // Data for a different event
        }
      } catch (e) {
        console.error("Error parsing pending purchase data from localStorage", e);
        localStorage.removeItem(storageKey);
      }
    }
  }, [event?.id, storageKey, reset]);

  // Clear localStorage when payment is confirmed
  useEffect(() => {
    if (paymentConfirmedByClientCheck && event?.id) {
      localStorage.removeItem(storageKey);
    }
  }, [paymentConfirmedByClientCheck, event?.id, storageKey]);


  const onSubmit: SubmitHandler<PurchaseFormValues> = async (data) => {
    setIsProcessing(true); 
    setPaymentConfirmedByClientCheck(false);
    
    const result = await initiatePurchaseAction({
      evento_id: event.id,
      ...data
    });

    if (result.success && result.pixCopyPaste && result.saleId && result.totalAmount && result.mpPaymentId && result.mpExternalReference) {
      setCurrentPixCode(result.pixCopyPaste);
      setCurrentQrCodeBase64(result.qrCodeBase64 || null);
      setCurrentSaleId(result.saleId); 
      setCurrentTotalAmount(result.totalAmount);
      setCurrentMpPaymentId(result.mpPaymentId);
      setCurrentMpExternalReference(result.mpExternalReference); // Store the MP external reference
      setShowPaymentDetails(true);

      const purchaseToStore: StoredPurchaseDetails = {
        eventId: event.id,
        pixCopyPaste: result.pixCopyPaste,
        qrCodeBase64: result.qrCodeBase64 || null,
        saleId: result.saleId,
        totalAmount: result.totalAmount,
        mpPaymentId: result.mpPaymentId,
        mpExternalReference: result.mpExternalReference,
        formValues: getValues(), // Store current form values
      };
      localStorage.setItem(storageKey, JSON.stringify(purchaseToStore));
      
      toast({
        title: 'PIX Gerado com Sucesso!',
        description: 'Copie o código PIX ou escaneie o QR Code para finalizar. A confirmação será automática.',
        variant: 'default',
        duration: 8000,
      });

    } else {
       toast({
        title: 'Erro ao Iniciar Compra',
        description: result.error || 'Não foi possível gerar os detalhes de pagamento. Tente novamente.',
        variant: 'destructive',
      });
    }
    setIsProcessing(false); 
  };

  const handleCopyPixCode = async () => {
    if (!currentPixCode) return;
    try {
      await navigator.clipboard.writeText(currentPixCode);
      toast({
        title: 'Código PIX Copiado!',
        description: 'O código PIX foi copiado para a área de transferência.',
        variant: 'default',
      });
    } catch (err) {
      console.error('Failed to copy PIX code: ', err);
      toast({
        title: 'Erro ao Copiar',
        description: 'Não foi possível copiar o código PIX. Tente manualmente.',
        variant: 'destructive',
      });
    }
  };

  const handleCheckPaymentStatus = async () => {
    if (!currentMpPaymentId) {
      toast({ title: 'Erro', description: 'ID de pagamento não encontrado para verificação.', variant: 'destructive' });
      return;
    }
    setIsCheckingStatus(true);
    const result = await checkPaymentStatusAction(currentMpPaymentId, currentMpExternalReference || undefined);
    setIsCheckingStatus(false);

    if (result.success && result.paymentStatus === 'approved') {
      setPaymentConfirmedByClientCheck(true); 
      localStorage.removeItem(storageKey); // Clear storage on confirmation
      if (result.organizerWhatsAppNumber && result.prefilledWhatsAppMessageToOrganizer) {
        const whatsappUrl = `https://wa.me/${result.organizerWhatsAppNumber}?text=${encodeURIComponent(result.prefilledWhatsAppMessageToOrganizer)}`;
        window.open(whatsappUrl, '_blank');
        toast({
          title: "Pagamento Confirmado!",
          description: result.message + " Você será redirecionado para o WhatsApp para informar o organizador.",
          variant: "default",
          duration: 10000,
        });
      } else {
         toast({
          title: 'Pagamento Aprovado!',
          description: result.message,
          variant: 'default',
        });
      }
    } else {
       toast({
        title: result.success ? 'Status do Pagamento' : 'Erro ao Verificar Status',
        description: result.message,
        variant: result.success ? 'default' : 'destructive',
      });
    }
  };
  
  const resetFormAndHideDetails = (clearStorage = true) => {
    setShowPaymentDetails(false);
    setCurrentPixCode(null);
    setCurrentQrCodeBase64(null);
    setCurrentSaleId(null);
    setCurrentMpPaymentId(null);
    setCurrentMpExternalReference(null);
    setPaymentConfirmedByClientCheck(false);
    setIsProcessing(false);
    form.reset(); 
    if (clearStorage && event?.id) {
        localStorage.removeItem(storageKey);
    }
  };


  if (showPaymentDetails && currentPixCode) {
    return (
      <Card className="w-full max-w-md mx-auto shadow-lg rounded-xl">
        <CardHeader className="p-6">
          <CardTitle className="text-2xl text-center text-primary flex items-center justify-center gap-2">
            <CreditCard className="h-7 w-7" /> {paymentConfirmedByClientCheck ? 'Pagamento Confirmado!' : 'Finalize seu Pagamento'}
          </CardTitle>
          {!paymentConfirmedByClientCheck && (
            <CardDescription className="text-center text-muted-foreground pt-1">
              Use o PIX Copia e Cola ou escaneie o QR Code abaixo no seu app bancário.
            </CardDescription>
          )}
        </CardHeader>
        <CardContent className="space-y-6 p-6">
          {paymentConfirmedByClientCheck ? (
            <div className="text-center space-y-4 p-4 bg-green-50 border border-green-200 rounded-md">
                <CheckCircle className="h-16 w-16 text-green-500 mx-auto" />
                <p className="text-xl font-semibold text-green-700">Seu pagamento foi aprovado!</p>
                <p className="text-sm text-green-600">A confirmação foi processada. Seus ingressos estão garantidos! Se aplicável, você foi redirecionado para notificar o organizador via WhatsApp.</p>
                 <Button onClick={() => resetFormAndHideDetails(true)} className="w-full mt-4">
                    Comprar Mais Ingressos
                </Button>
            </div>
          ) : (
            <>
              {currentQrCodeBase64 ? (
                <div className="flex justify-center p-4 bg-muted rounded-lg">
                  <Image 
                    src={`data:image/png;base64,${currentQrCodeBase64}`}
                    alt={`QR Code para pagamento do evento ${event.nome_evento}`} 
                    width={200}
                    height={200}
                    className="rounded-md shadow-md"
                    data-ai-hint="payment QR code"
                  />
                </div>
              ) : (
                <div className="flex justify-center p-4 bg-muted rounded-lg relative h-48 w-full">
                  <Image 
                    src={event.imagem_url || `https://picsum.photos/seed/${event.id}/400/200`}
                    alt={`Imagem do evento ${event.nome_evento}`} 
                    layout="fill"
                    objectFit="cover"
                    className="rounded-md shadow-md"
                    data-ai-hint="event image banner"
                  />
                </div>
              )}
              <div className="space-y-2">
                <Label htmlFor="pixCode" className="text-sm text-muted-foreground">PIX Copia e Cola (Mercado Pago):</Label>
                <div className="flex items-center gap-2">
                    <Textarea 
                        id="pixCode" 
                        readOnly 
                        value={currentPixCode} 
                        className="text-xs bg-muted p-2 rounded-md h-auto flex-grow resize-none" 
                        rows={3}
                    />
                    <Button variant="outline" size="icon" onClick={handleCopyPixCode} aria-label="Copiar código PIX">
                        <ClipboardCopy className="h-4 w-4" />
                    </Button>
                </div>
              </div>
              
              <p className="text-center font-semibold text-lg text-foreground">Valor Total: R$ {currentTotalAmount.toFixed(2).replace('.', ',')}</p>
              
              <div className="p-4 bg-blue-50 border border-blue-200 rounded-md text-sm text-blue-700">
                <div className="flex items-start">
                    <Info className="h-5 w-5 mr-2 mt-0.5 shrink-0" />
                    <div>
                        <p className="font-semibold">Aguardando Pagamento</p>
                        <p>Após efetuar o pagamento PIX, a confirmação será processada automaticamente pelo nosso sistema (via webhook). Você pode usar o botão abaixo para uma verificação manual.</p>
                        <p className="mt-1">Se você fechar esta página, pode acompanhar o status da sua compra através do seu e-mail ou contato com o organizador caso tenha problemas.</p>
                    </div>
                </div>
              </div>

              <Button onClick={handleCheckPaymentStatus} className="w-full" variant="secondary" disabled={isCheckingStatus}>
                {isCheckingStatus ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <RefreshCw className="mr-2 h-4 w-4" />}
                Verificar Status do Pagamento
              </Button>

              <Button variant="outline" onClick={() => resetFormAndHideDetails(true)} className="w-full text-base py-3 rounded-lg" disabled={isProcessing || isCheckingStatus}>
                Voltar e Alterar Dados (Cancela este PIX)
              </Button>
            </>
          )}
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="w-full max-w-lg mx-auto shadow-xl rounded-xl">
      <CardHeader className="p-6">
        <CardTitle className="text-3xl text-center text-primary flex items-center justify-center gap-2">
            <ShoppingCart className="h-8 w-8" /> Comprar Ingresso
        </CardTitle>
        <CardDescription className="text-center text-muted-foreground pt-1">
          Preencha seus dados para adquirir ingressos para "{event.nome_evento}".
        </CardDescription>
      </CardHeader>
      <CardContent className="p-6">
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          <div>
            <Label htmlFor="nome_comprador" className="text-foreground font-medium">Nome Completo</Label>
            <Input id="nome_comprador" {...register('nome_comprador')} className="mt-1 bg-background border-input focus:ring-primary focus:border-primary rounded-md" />
            {errors.nome_comprador && <p className="text-sm text-destructive mt-1">{errors.nome_comprador.message}</p>}
          </div>

          <div>
            <Label htmlFor="email_comprador" className="text-foreground font-medium">E-mail</Label>
            <Input id="email_comprador" type="email" {...register('email_comprador')} className="mt-1 bg-background border-input focus:ring-primary focus:border-primary rounded-md" />
            {errors.email_comprador && <p className="text-sm text-destructive mt-1">{errors.email_comprador.message}</p>}
          </div>

          <div>
            <Label htmlFor="whatsapp" className="text-foreground font-medium">WhatsApp (somente números)</Label>
            <Input id="whatsapp" type="tel" {...register('whatsapp')} className="mt-1 bg-background border-input focus:ring-primary focus:border-primary rounded-md" placeholder="Ex: 11988887777" />
            {errors.whatsapp && <p className="text-sm text-destructive mt-1">{errors.whatsapp.message}</p>}
          </div>

          <div>
            <Label htmlFor="quantidade" className="text-foreground font-medium">Quantidade de Ingressos</Label>
             <Controller
                name="quantidade"
                control={control}
                defaultValue={1}
                render={({ field }) => (
                  <Select
                    onValueChange={(value) => field.onChange(parseInt(value))}
                    value={field.value?.toString()}
                    disabled={maxTickets === 0}
                  >
                    <SelectTrigger id="quantidade" className="w-full mt-1 bg-background border-input focus:ring-primary focus:border-primary rounded-md">
                      <SelectValue placeholder="Selecione a quantidade" />
                    </SelectTrigger>
                    <SelectContent>
                      {Array.from({ length: maxTickets }, (_, i) => i + 1).map(num => (
                        <SelectItem key={num} value={num.toString()}>
                          {num} ingresso{num > 1 ? 's' : ''}
                        </SelectItem>
                      ))}
                      {maxTickets === 0 && <SelectItem value="0" disabled>Esgotado</SelectItem>}
                    </SelectContent>
                  </Select>
                )}
              />
            {errors.quantidade && <p className="text-sm text-destructive mt-1">{errors.quantidade.message}</p>}
            {maxTickets === 0 && !errors.quantidade && <p className="text-sm text-destructive mt-1">Não há ingressos disponíveis.</p>}
          </div>
          
          <div className="space-y-1 text-right text-sm text-muted-foreground pt-2">
            <p>Ingressos: R$ {totalEventPrice.toFixed(2).replace('.', ',')}</p>
            <p>Taxa de Serviço: R$ {totalServiceFee.toFixed(2).replace('.', ',')}</p>
          </div>
          <div className="text-xl font-semibold text-right text-primary">
            Total: R$ {finalPurchaseAmount.toFixed(2).replace('.', ',')}
          </div>

          <Button type="submit" className="w-full bg-accent hover:bg-accent/90 text-accent-foreground font-semibold text-base py-3 rounded-lg" disabled={isProcessing || maxTickets === 0}>
            {isProcessing ? <Loader2 className="mr-2 h-5 w-5 animate-spin" /> : <CreditCard className="mr-2 h-5 w-5" />}
            Gerar PIX para Pagamento
          </Button>
        </form>
      </CardContent>
    </Card>
  );
}

